
.movies .movie {
    width: 45%;
    background-color: rgb(255, 237, 220);
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color: #7d7d7d;
    box-shadow: 0 13px 27px -5px #2b5036b8,
      0 8px 16px -8px rgba(161, 26, 26, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  }
  
  .movie img {
    position: relative;
    top: -50px;
    max-width: 150px;
    width: 100%;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
      0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
  }
  .movie .movie__title {
    margin: 0;
    margin-bottom: 5px;
    font-size: 24px;
    font-weight: 800;
    color: #161616;
  }
  
.movie .movie__year {
  color: #949494;
}


.movie .movie__genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  margin: 5px 0px;
}

.movie__genres li,
.movie .movie__year {
  margin-right: 10px;
  font-size: 12px;
}

.movie .movie__summary{
  font-size: 13px;
  color: #5f340c;
}